body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.nav {
	background-size: cover;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-position: top;
	text-align: center;
	height: auto;
}

.navlogo {
	align-content: center;
	width: 80%;
	height: auto;
	align-content: center;
	padding-top: 40px;
	padding-bottom: 20px;
}

.content {
	color: white;
	background-color: #336633;
	font-family: "Pacifico", cursive;
	border: 2px;
	border-style: solid;
	border-color: #336633;
	border-width: 50px;
}

.studentInfo {
	font-size: 20px;
}
.sendButton {
	color: #336633;
	background-color: #ffffcc;
	border: 2px solid #4caf50;
	font-family: "Pacifico", cursive;
}

#classComponent {
	padding: 20px;
}

#studentCollection {
	background: #ffffcc;
}

.studentComponent {
	font-size: 2rem;
}

i.icon-creamyyy {
	color: #ffffcc;
}

.studentname {
	color: white;
	font-family: "Pacifico", cursive;
	font-size: 18px;
}

.studentheader {
	color: white;
	font-family: "Pacifico", cursive;
}

#modal1 {
	background-color: #ffffcc;
}

.modal-footer {
	background-color: #336633;
}

.chattydiv {
	color: #336633;
	font-family: "Pacifico", cursive;
}

.card-title {
	color: #336633;
}

.messages {
	color: #336633;
}

.user-view {
	color: #336633;
}

#nm {
	color: #336633;
}

.chatContainer {
	position: relative;
}
.chatportal {
	position: fixed;
	bottom: 0;
	width: 75%;
	height: 12rem;
	color: #336633;
}
.messages-header {
	color: white;
}
.user-view {
	background-color: #336633;
}

.sendButton {
	color: #336633;
	background-color: #ffffcc;
	border: 2px solid #4caf50;
	font-family: "Pacifico", cursive;
}

.input-field {
	color: #336633;
}

.teachername {
	color: white;
	font-size: 36px;
	font-family: "Pacifico", cursive;
}

.sideNavContainer {
	display: flex;
	justify-content: center;
}

.sidenav {
	background: #ffffcc;
	border: thick;
	border-color: #336633;
}

.collapsible-body {
	color: #336633;
	background: #336633;
}
.text-email {
	color: #ffffcc;
}
.text-name {
	color: #ffffcc;
}
.collapsible-header {
	text-align: center;
}
.dropdownHeader {
	color: #336633;
	font-family: "Pacifico", cursive;
	justify-content: center;
}

.StudentList {
	margin-top: 20px;
}

.validate {
    color: white;
}
@media (max-width: 1100px) {
    #squarelogo {
        width: 70%;
        height: auto;
        align-content: center;
        padding-top: 40px;
    }
}

@media (min-width: 1101px) {
    #squarelogo {
        width: 30%;
        height: auto;
        align-content: center;
        padding-top: 60px;
    }
}

html,
body {
	background-image: url("/blackboardbg-min.jpg");
	background-position: center;
	background-size: cover;
	background-color: #336633;
}

.contentdiv {
	min-height: calc(100vh - 50px);
}

